.dpit-admin-btn {

	position: relative;

	cursor: pointer;
	padding: 11px 20px;
	border-radius: 5px;
	transition: all 0.5s ease;

	box-shadow: 0 0 5px rgba(var(--dpu-color-3), .25);
	background-color: rgba(var(--dpu-color-3), 0.951);
	text-decoration: none;
	text-transform: uppercase;
	display: flex;
	justify-content: center;
	text-align: center;


	&--margin-right {
		margin-right: 15px;
	}

	&__text {
		font-size: 14px;
		font-weight: bold;
		color: rgba(var(--dpu-color-3-text), 1);
		transition: all 0.5s ease;
	}

	&__inactive-text-2,
	&__inactive-text {
		text-transform: none;
		//font-size: 13px;
		font-size: 15px;
		color: rgba($text-color-dark, 1);
		transition: all 0.5s ease;
		display: none;
	}

	&.in-process {
		pointer-events: none;

		.dpit-admin-btn__text {
			color: transparent;
		}
	}

	&:hover {
		background-color: rgba(var(--dpu-color-3), 0.75);
	}


	&__bullets {
		pointer-events: none;
		position: absolute;
		display: flex;
		justify-content: center;
		top: 45%;
		left: 50%;
		transform: translate(-50%, -50%);
		padding: 3px !important;
		transition: all 0.5s ease-in-out;

		&.inactive {
			opacity: 0 !important;
		}

		ul {
			margin: 13px auto 1px auto;
			padding: 0;
			display: flex;
			justify-content: center;

			li {
				list-style: none;
				margin: 0 2px 5px 2px;
				width: 8px;
				height: 8px;

				display: block;

				background-color: rgba(var(--dpu-color-3-text), 0.336);
				border-radius: 50%;
				text-decoration: none;
				cursor: pointer;
				outline: none;
				@include transitionMultiFast(transform, background-color);

				&.active {
					background-color: rgba(var(--dpu-color-3-text), 1) !important;
					transform: translateY(-100%);
				}

				&.semi-active {
					transform: translateY(-50%);
				}
			}
		}
	}



	&--settings {
		margin-right: 12px !important;
		min-width: 120px;
		box-shadow: none;

		&.in-process {
			.dpit-admin-btn__bullets {
				ul {
					li {
						background-color: rgba($text-color-dark, 0.336);

						&.active {
							background-color: rgba($text-color-dark, 1) !important;
						}
					}
				}
			}
		}

		.dpit-admin-btn__text {
			font-weight: 600;
			color: $text-color-dark;
		}

		.dpit-admin-btn__bullets {
			ul {
				li {
					background-color: rgba(var(--dpu-color-2-text), 0.336);

					&.active {
						background-color: rgba(var(--dpu-color-2-text), 1) !important;
					}
				}
			}
		}

	}

	&--error {
		pointer-events: none;

		.dpit-admin-btn__text {
			color: rgba(var(--dpu-color-r), 1) !important;
		}
	}

	&--success {
		pointer-events: none;

		.dpit-admin-btn__text {
			color: rgba(var(--dpu-color-g), 1) !important;
		}
	}

	&--trigger {
		box-shadow: 0 0 5px rgba(var(--dpu-color-4), .25);
		background-color: rgba(var(--dpu-color-4), 0.951);

		&:hover {
			background-color: rgba(var(--dpu-color-4), 0.75);
		}

		.dpit-admin-btn__text {
			color: rgba(var(--dpu-color-4-text), 1);
		}

		.dpit-admin-btn__bullets {
			ul {
				li {
					background-color: rgba(var(--dpu-color-4-text), 0.336);

					&.active {
						background-color: rgba(var(--dpu-color-4-text), 1) !important;
					}
				}
			}
		}
	}

	&--mobile-menu {

		box-shadow: 0 0 5px rgba(var(--dpu-color-2), .25);
		background-color: rgba(var(--dpu-color-2), 0.951);

		&:hover {
			background-color: rgba(var(--dpu-color-2), 0.75);
		}

		.dpit-admin-btn__text {
			color: rgba(var(--dpu-color-2-text), 1);
		}

		.dpit-admin-btn__bullets {
			ul {
				li {
					background-color: rgba(var(--dpu-color-2-text), 0.336);

					&.active {
						background-color: rgba(var(--dpu-color-2-text), 1) !important;
					}
				}
			}
		}
	}
}

.dpit-admin-btn-for-reload {
	&.inactive {
		//opacity: 0.6;
		//pointer-events: none;
		//cursor: default;
		background-color: rgba(red, 0.2);
		border: solid 1px red;

		.dpit-admin-btn__text {
			display: none;
		}

		&:not(.inactive--var-url) {
			.dpit-admin-btn__inactive-text {
				display: unset;
			}
		}

		&.inactive--var-url {
			.dpit-admin-btn__inactive-text-2 {
				display: unset;
			}
		}
	}
}

.dpit-admin-btn-wrap--settings {
	max-width: 490px;
}