.dpit-notice {

	max-width: 750px;

	&__link {
		//display: block;
		margin-right: 15px;

		&:last-of-type {
			margin-bottom: 17px;
			display: inline-block;
		}

		&#a4r-link-OK {
			margin-bottom: 8px;
			padding-right: 50px;
			padding-left: 50px;
			font-weight: 600;
		}
	}

	&__text {
		max-width: 700px;
		padding-bottom: 10px !important;
	}

	&__button {
		margin-bottom: 17px !important;

		&:not(.button-secondary) {
			background-color: rgba(var(--dpu-color-3), 1) !important;
			color: rgba(var(--dpu-color-3-text), 1) !important;
		}

		border: none !important;
		transition: background-color 0.3s ease-in-out;

		&:hover {
			background-color: rgba(var(--dpu-color-3), 0.85) !important;
		}

		font-weight: 600;

		&__sub-text {
			margin-top: -8px;
			display: block;
			font-size: 10px;
			font-weight: 500;
			color: rgba(var(--dpu-color-3-text), 0.9);
			text-transform: uppercase;
			line-height: 2.5;
		}

		&--upper-case {
			text-transform: uppercase;
		}

		&--secondary {
			background-color: rgba(var(--dpu-color-2), 1) !important;
			color: rgba(var(--dpu-color-2-text), 1) !important;

			&:hover {
				background-color: rgba(var(--dpu-color-2), 0.85) !important;
			}

			.dpit-notice__button__sub-text {
				color: rgba(var(--dpu-color-2-text), 0.9);
			}
		}

		&.button-pro-promo {
			font-weight: 600;
			@include transitionFast(background-color);
		}
	}

	&__img {
		&.dpit-a4r-5star-img {
			max-width: 120px;
			margin: 7px 0 -7px;
		}
	}

	&--2-buttons {
		.dpit-notice__button:first-of-type {
			margin-right: 10px;
		}
	}
}


.dpit-notice-container {
	max-height: 200px;
	overflow-y: auto;
}